function translit(word){
    if(word === undefined) word = 'a'

    word = word.trim().split(' ').map( el => el[0].toLowerCase() + el.slice(1)).join('-')
    var answer = '';
    var converter = {
        '': 'a',    '': 'b',    '': 'v',    '': 'g',    '': 'd',
        '': 'e',    '': 'e',    '': 'zh',   '': 'z',    '': 'i',
        '': 'y',    '': 'k',    '': 'l',    '': 'm',    '': 'n',
        '': 'o',    '': 'p',    '': 'r',    '': 's',    '': 't',
        '': 'u',    '': 'f',    '': 'h',    '': 'c',    '': 'ch',
        '': 'sh',   '': 'sch',  '': '',     '': 'y',    '': '',
        '': 'e',    '': 'yu',   '': 'ya',

        '': 'A',    '': 'B',    '': 'V',    '': 'G',    '': 'D',
        '': 'E',    '': 'E',    '': 'Zh',   '': 'Z',    '': 'I',
        '': 'Y',    '': 'K',    '': 'L',    '': 'M',    '': 'N',
        '': 'O',    '': 'P',    '': 'R',    '': 'S',    '': 'T',
        '': 'U',    '': 'F',    '': 'H',    '': 'C',    '': 'Ch',
        '': 'Sh',   '': 'Sch',  '': '',     '': 'Y',    '': '',
        '': 'E',    '': 'Yu',   '': 'Ya'
    };

    for (var i = 0; i < word.length; ++i ) {
        if (converter[word[i]] == undefined){
            answer += word[i];
        } else {
            answer += converter[word[i]];
        }
    }

    return answer;
}

export default translit